
@import "../../../assets/css/normalize.css";
@import "../../../assets/css/pocketgrid.css";
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
@font-face {
  font-family: 'Segment7Standard';
  src: url('../../../../static/fonts/Segment7Standard.otf') format('opentype');
  font-weight: normal;
  font-style: italic;
}
html {
  background-color: #26292e;
}
.warning-strip {
  background: repeating-linear-gradient(-45deg, #bfa50c, #bfa50c 15px, #2d2d2d 15px, #2d2d2d 31px), url(../../../../static/img/grunge_overlay.png) center repeat;
  background-blend-mode: multiply;
}
.danger-strip {
  background: repeating-linear-gradient(-45deg, #ee321b, #ee321b 15px, #390a04 15px, #390a04 31px), url(../../../../static/img/grunge_overlay.png) center repeat;
  background-blend-mode: multiply;
}
i.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  position: relative;
  top: 0.2em;
  margin-right: 0.2em;
  background-size: contain;
  background-position: center;
}
i.icon.check,
i.icon.normal {
  background-image: url('../../../assets/svg/icons/check.svg');
}
i.icon.exclamation-orange,
i.icon.warning {
  background-image: url('../../../assets/svg/icons/exclamation_orange.svg');
}
i.icon.exclamation-red,
i.icon.critical {
  background-image: url('../../../assets/svg/icons/exclamation_red.svg');
}
html {
  background: #26292e;
}
.engineering {
  width: 1024px;
  margin: 0 auto;
}
.engineering .content {
  padding: 15px;
  background: #2f3440;
  color: #ebebeb;
}
.engineering h1 {
  margin: 0 0 0.25em;
}
.engineering h1 .secondary {
  font-size: 0.5em;
}
.engineering h1 .secondary.critical {
  color: #ff4629;
}
.engineering h1 .secondary.warning {
  color: #f7b233;
}
.engineering h1 .secondary.normal {
  color: #37ee37;
}
.engineering .label {
  padding: 6px 10px;
  border-radius: 4px;
  display: inline-block;
  text-align: center;
  font-family: monospace;
  font-size: 1.25em;
  color: #ebebeb;
  background-color: #242831;
}
.engineering .label.secondary {
  color: #6b6b6b;
  background-color: #262a34;
}
.engineering {
  /* Disable text selection ('cause it looks bad) */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  /* If we didn't use this wrapper and instead used margin-top on .alerts, there'd be a gap between
     .status and .alerts, closing the popup if moving the pointer down into it */
}
.engineering nav {
  padding: 5px;
  background: repeating-linear-gradient(-45deg, #bfa50c, #bfa50c 15px, #2d2d2d 15px, #2d2d2d 31px), url(../../../../static/img/grunge_overlay.png) center repeat;
  background-blend-mode: multiply;
}
.engineering nav .nav-item-wrapper {
  padding: 2px;
}
.engineering nav .nav-item-wrapper,
.engineering nav .nav-item-wrapper:focus {
  outline: none;
}
.engineering nav .nav-item,
.engineering nav .status {
  display: block;
  border-radius: 6px;
  color: #ebebeb;
  background-color: #191c23;
  text-decoration: none;
}
.engineering nav .nav-item,
.engineering nav .status,
.engineering nav .nav-item:focus,
.engineering nav .status:focus {
  outline: none;
}
.engineering nav .nav-item {
  padding: 10px;
  text-align: center;
}
.engineering nav .nav-item.router-link-exact-active {
  color: #ebebeb;
  background-color: #454c5d;
}
.engineering nav .status {
  margin-top: 4px;
  border: 2px solid #454c5d;
  padding: 4px;
  font-size: 0.8em;
}
.engineering nav .status.critical {
  border-color: #704f4b;
  background-color: #ff4629;
}
.engineering nav .status.warning {
  border-color: #696255;
  background-color: #c28c2f;
}
.engineering nav .status.normal {
  border-color: #4c5e4c;
  background-color: #2cb12c;
}
.engineering .alerts-wrapper {
  position: absolute;
  z-index: 100;
  padding-top: 8px;
  margin-left: -6px;
}
.engineering .alerts {
  border: 2px solid #191c23;
  padding: 10px;
  border-radius: 6px;
  box-shadow: 0 0 10px 2px rgba(25, 28, 35, 0.75);
  background-color: #2f3440;
  font-size: 0.9rem;
  display: none;
}
.engineering .alerts.visible {
  display: block;
}
.engineering .alerts ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.engineering .alerts ul li + li {
  margin-top: 0.5em;
}
@keyframes offline-popup-glow {
0% {
    opacity: 0.5;
}
90% {
    opacity: 1;
}
}
.engineering .offline-popup {
  /* Stick to viewport bottom */
  position: fixed;
  bottom: 0;
  /* Stretch to full page width */
  left: 0;
  right: 0;
  z-index: 100;
  opacity: 1;
  transition: opacity 0.25s, bottom 0.25s;
  /* We're only animating the opacity of this pseudo element because animating box-shadow is slow */
}
.engineering .offline-popup .popup-content {
  position: relative;
  pointer-events: auto;
  background-color: #ca3624;
  color: #ebebeb;
  /* Limit to page width and center */
  width: 1024px;
  margin: 0 auto;
  padding: 0.5em;
}
.engineering .offline-popup .popup-content::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 90;
  content: '';
  box-shadow: 0 0 15px 4px rgba(255, 70, 41, 0.75);
  animation: 0.5s infinite alternate offline-popup-glow;
}
.engineering .offline-popup:hover {
  opacity: 0.25;
  bottom: -3.25em;
  padding-top: 3.25em;
}
.engineering .offline-popup:hover .popup-content {
  pointer-events: none;
}



